const {gamestate} = require('./gamestate');


export class Room {
    constructor(id){
        this.id = id;
        this.connections = [];
        this.entered = false;
    }

    connectRoom(room){
        if(this.connections.indexOf(room) == -1){
            this.connections.push(room);
            room.connectRoom(this);
        }
    }

    enter(terminal, prev){
        console.log(`Entered room ${this.id}. I have entered before: ${this.entered}. Connections: ${this.connections.map(c => c.id)}`);

        //terminal.clear();

        terminal.writeln('');
        terminal.writeln('');

        // if(prev != null && this.entered && prev.id === this.id){
        //     this.writeDescription(terminal);
        // }
        if(!this.entered){
            this.entered = true;
            this.writeEnterText(terminal);
            terminal.writeln('');
            terminal.writeln('');
            this.writeDescription(terminal);
        }
        terminal.writeln('');
        terminal.writeln('');
        terminal.writeln('Vad gr du?');
        terminal.writeln('');
        this.writeActions(terminal);
    }

    writeDescription(terminal){
        terminal.writeln('Ett helt kalt rum. Lskigt!');
    }

    writeEnterText(terminal){
        terminal.writeln('Ett helt kalt rum. Lskigt!');
    }

    writeActions(terminal){

    }

    takeAction(actionId, terminal){
        return this;
    }

    
}

export class Bedroom extends Room{
    constructor(){
        super('bedroom');
    }

    writeDescription(terminal){
        terminal.writeln('Du befinner dig i ett sovrum');
        terminal.writeln('Dom mrka gardinerna r frdragna men en tunn ljusstrimma syns dr tyget mter vggen');
        terminal.writeln('Mitt i rummet str en stor sng med senapsgula sngklder');
        terminal.writeln('P en byr vid fotndan av sngen str en trta. De 25 ljusen verkar ha precis blivt utblsta');
        terminal.writeln('En stor palm str sttlig p en bl pall i hrnet av rummet');
        terminal.writeln('Bredvid sngen str en rd ftlj. Diverse klder str runt ');
        terminal.writeln('En drr in till nsta rum str lite p glnt');
    }

    writeEnterText(terminal){
        terminal.writeln('Efter en god natts smn vaknar du p denna speciella dag av sknsng.');
        terminal.writeln('Rummet r fylld av en orange gld av 25(!) ljus p en trta.');
        terminal.writeln('Du blser ut ljusen och knner lukten av rk och stearin');
    }

    writeActions(terminal){
        if(!gamestate.book){
            terminal.writeln('1. Titta nrmare p trtan');
        }
        terminal.writeln('2. ppna drren som str lite p glnt och g ut ur den');

    }

    takeAction(actionId, terminal){
        switch(actionId){
            case 1:
                terminal.writeln('Nr du tittar nrmare p trtan knner du doften av citroner. Rda fina jordgubbar sitter p toppen.');
                terminal.writeln('Lite bakom trtan ligger en bok. Nr du tittar nrmare p den s ser du att den innehller en massa frslag p ventyr');
                terminal.writeln('Men fr att ka p snna ventyr behvs ju lite utrustning...');
                gamestate.book = true;
                return this;
            case 2:
                terminal.writeln('Du ppnar drren som stod lite p glnt...');
                return this.connections[0];
            default:
                terminal.writeln('Det r svrt att bestmma sig nr man r nyvaken. Du bestmmer dig fr att tnka igenom allt lite igen...');
                return this;
        }
    }
}

export class LivingRoom extends Room{


    constructor(){
        super('livingroom');

    }

    writeDescription(terminal){
        terminal.writeln('Du befinner dig i ett vardagsrum');
        terminal.writeln('En stor mysig soffa str lngs vggen med ett soffbord av tr framfr sig. ');
        terminal.writeln('I fnstret str en massa vxter. Dom ger rummet en hrlig knsla av liv och frg.');
        terminal.writeln('Bredvid soffan str ett runt bord med 4 grna stolar runt sig.');
        if(!gamestate.clothes){
            terminal.writeln('P bordet ligger ett paket inslaget med grnt papper. Vad kan det vara?');
        }
        else{
            terminal.writeln('P bordet ligger grnt omslagspapper i en liten hg.');
        }
        terminal.writeln('Du ser en drrppning in till ett kk');
        terminal.writeln('Bredvid denna finns en glasdrr. Genom fnstret ser du en hall');
        terminal.writeln('Mitt emot soffan finns en drr in till sovrummet')
    }

    writeEnterText(terminal){
        terminal.writeln('Detta rummet r mycket ljusare');
        terminal.writeln('Du knner hur du vaknar till');
        terminal.writeln('Ngonting mjukt stryker sig frbi ditt ben. En lite katt! Katten tittar p dig och buffar lite ltt med huvudet mot ditt ben');
    }

    writeActions(terminal){
        if(!gamestate.clothes){
            terminal.writeln('1. ppna paketet p bordet');
        }
        terminal.writeln('2. G in i kket');
        terminal.writeln('3. G in i hallen');
        terminal.writeln('4. G in i sovrummet');
        terminal.writeln('5. Klappa den lilla katten');
    }

    takeAction(actionId, terminal){
        switch(actionId){
            case 1:
                terminal.writeln('Med ivriga fingrar sliter du upp presentpappret, som flyger t alla hll.')
                terminal.writeln('Du ser en jacka och ett par vandrings byxor. Jackan ser vattentt ut, men att den verkar nd andas och den r precis i den frgen du gillar');
                terminal.writeln('Byxorna ser perfekta ut fr att g runt i skogsdungar. De har till och med lite extra fickor')
                terminal.writeln('Nr du tar p dig klderna knner du dig lite mer redo fr att ta dig ut p ventyr...');
                gamestate.clothes = true;
                return this;
            case 2:
                terminal.writeln('Nyfiket gr du in i kket...');
                return this.connections[0];
            case 3:
                terminal.writeln('Med bestmda steg gr du fram och ppnar drren in till hallen...');
                return this.connections[1];
            case 4:
                terminal.writeln('Sngen lockar, du gr in i sovrummet...');
                return this.connections[2];
            case 5:
                terminal.writeln('Den lilla katten stryker sig mot din utstrkta hand. Efter ett liten stund hr du en svagt spinnande frn hennes mage');
                return this;
            default:
                terminal.writeln('Det r svrt att bestmma sig nr man r nyvaken. Du bestmmer dig fr att tnka igenom allt lite igen');
                return this;
        }
    }
}

export class Kitchen extends Room{
    constructor(){
        super('kitchen');
    }

    writeDescription(terminal){
        terminal.writeln('Du befinner dig i ett kk');
        if(!gamestate.breakfast){
            terminal.writeln('P kksbnken str det mat uppdukat.');
            terminal.writeln('Yoghurt, brd, ost, smr och annat plgg.');
            terminal.writeln('Te str i en liten terrakotta kanna');
            terminal.writeln('Tv koppar och tv sm tallrikar str redan framme');
        }
        else{
            terminal.writeln('Resterna av den goda frukosten str fortarande framme.');
            terminal.writeln('Hoppas ngon diskar');
        }
    }

    writeEnterText(terminal){
        terminal.writeln('Innan man kan ge sig ut p ventyr mste man ha frukost tnker du.');
        terminal.writeln('Men vad ska man ta?')
    }

    writeActions(terminal){
        if(!gamestate.breakfast){
            terminal.writeln('1. t frukost');
        }
        terminal.writeln('2. G tillbaka in i vardagsrummet');
    }

    takeAction(actionId, terminal){
        switch(actionId){
            case 1:
                terminal.writeln('Frukosten sitter extra bra nr man vet att det finns trta eftert. Den sker ju ocks i gott sllskap');
                gamestate.breakfast = true;
                return this;
            case 2:
                terminal.writeln('Vad mer kan det finnas i lgenheten? Du gr in i vardagsrummet igen');
                return this.connections[0];
            default:
                terminal.writeln('Det r svrt att bestmma sig nr man r nyvaken. Du bestmmer dig fr att tnka igenom allt lite igen...');
                return this;
        }
    }
}

export class Hallway extends Room{
    constructor(){
        super('hallway');
    }

    writeDescription(terminal){
        terminal.writeln('Du befinner dig i hallen');
        terminal.writeln('Garderober fyllda med saker finns p hger sida');
        terminal.writeln('Framfr dig ser du en ytterdrr. r det dags att g ut p ventyr?');
    }

    writeEnterText(terminal){
        terminal.writeln('En fin dag som denna mste man ju ut tnker du nr du gr in i hallen')
    }

    writeActions(terminal){
        terminal.writeln('1. G ut genom ytterdrren');
        terminal.writeln('2. G tillbaka in i vardagsrummet');
    }

    takeAction(actionId, terminal){
        switch(actionId){
            case 1:
                if(!gamestate.clothes){
                    terminal.writeln('Innan man gr p ventyr behvs ju lite klder! Nog bst att fixa det frst');
                    return this;
                }

                if(!gamestate.breakfast){
                    terminal.writeln('Precis innan du ska g ut genom drren kurrar magen till. Lite frukost skulle sitta fint, tnker du.');
                    return this;
                }

                if(!gamestate.book){
                    terminal.writeln('Vad ska du gra dr ute? Det hade varit bra med en karta eller tv frst');
                    return this;
                }

                terminal.writeln('Helt kittad och med mat i magen ppnar du ytterdrren. Tnk vad roligt man kan ha det ute i naturen.');
                return this.connections[0];
            case 2:
                terminal.writeln('Inte riktigt dags fr ventyr n. Du gr in i vardagsrummet igen...');
                return this.connections[1];
            default:
                terminal.writeln('Det r svrt att bestmma sig nr man r nyvaken. Du bestmmer dig fr att tnka igenom allt lite igen...');
                return this;
        }
    }
}

