export function initializeRooms(){
    const bedroom = new Bedroom();
    const livingRoom = new LivingRoom();
    const kitchen = new Kitchen();
    const hallway = new Hallway();
    const adventure = new Adventure();
    hallway.connectRoom(adventure);
    livingRoom.connectRoom(kitchen);
    livingRoom.connectRoom(hallway);
    bedroom.connectRoom(livingRoom);

    return {bedroom, livingRoom, kitchen, hallway, adventure};
}
